* {
  margin: 0;
}

.container {
  padding: 0 30px;
}

.personalization-content {
  display: flex;
  margin: 30px;
  border-radius: 8px;
}

.profile-content,
.account-content {
  width: 50%;
}

.profile-header,
.account-header {
  padding: 18px 33px;
}

.profile-header {
  background-color: #E9F7EA;
  border-top-left-radius: 8px;
}

.account-header {
  background-color: #FBEFFD;
  border-top-right-radius: 8px;
}

.profile-title,
.account-title {
  font-size: 20px;
  line-height: 16px;
  font-weight: 700;
}

.profile-title {
  color: #40C057;
}

.account-title {
  color: #BE4BDB;
}

.personalization-body {
  padding: 20px 33px;
  border: 1px solid #EAECEF;
}
.radius-left{
  border-bottom-left-radius: 8px;
}
.radius-right {
  border-bottom-right-radius: 8px;
}
.checkmark {
  width: 20px;
  height: 20px;
  display: inline-block;
  border: 1px solid gray;
  border-radius: 6px;
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}

.checkbox-item {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

/* .checkbox.active {
  background-color: #40C057;
  border-color: transparent;
  transition: all 0.3s ease-in-out;
  color: #fff;
} */
.checkbox-label.active{
  font-weight: 500;
}
.checkmark i {
  font-size: 13px;
  opacity: 0;
}
.checkbox.active i{
  opacity: 1;
}
.checkbox-label {
  color: #898989;
  font-weight: 400;
  line-height: 15px;
  margin-left: 15px;
}
.checkbox-item:last-child{
  margin-bottom: 0;
}
.disabled-status {
  background-color: #FFE8E8;
  border-radius: 15px;
  font-size: 14px;
  font-weight: 600;
  padding: 3.5px 10px;
  display: inline-block;
  color: #FB7775;
  margin-left: 6px;
}
.disable-check .checkmark {
  background-color: #eaeaea;
  border-color: #dbdbdb;
}
.disable-check .checkbox-label {
  color: #bebebe;
}
.account-checkbox.active {
  background-color: #BE4BDB;
}
.disable-check .checkmark i {
  opacity: 1;
  color: #c2c2c2;
}

.checkbox-div{
  height: 30px !important;
}
.checkbox-item input:checked~.checkmark {
  background-color: #40C057;
  border-color: transparent;
}
.account-content  .checkbox-item input:checked~.checkmark {
  background-color: #BE4BDB;
}
.checkbox-item .checkmark::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 5px;
  bottom: 0;
  margin: auto;
  height: 9px;
  border: 1px solid #fff;
  border-width: 0 2px 2px 0;
  transform: translate(-50%, -50%) rotate(45deg);
}
.checkbox-input-check{
  position: relative;
}
.checkbox-input {
  position: absolute;
  top: 0;
  left: 0px;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 2;
}
.disable-check .checkmark::after {
  opacity: 0;
}
.only-check input:checked~.checkmark {
  background-color: #eaeaea !important;
}
.only-check .checkmark::after {
  opacity: 1;
}
